import { store } from '../store';
import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router';
import Home from '../views/Home.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: { requireLogin: true }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/usecar',
    name: 'Use car',
    component: () => import('../views/UseCar.vue'),
    meta: { transition: 'expand-card', requireLogin: true },
  },
  {
    path: '/leavecar',
    name: 'Leave car',
    component: () => import('../views/LeaveCar.vue'),
    meta: { transition: 'expand-card', requireLogin: true },
  },
  {
    path: '/admin',
    name: 'Admin',
    component: () => import('../views/Admin.vue'),
    meta: { transition: 'expand-card', requireLogin: true },
    children: [
      { path: '', component: () => import('../views/AdminHome.vue'), meta: { transition: 'expand-card', requireLogin: true } },
      { path: 'notifications', component: () => import('../views/AdminNotifications.vue'), meta: { transition: 'expand-card', requireLogin: true } },
      { path: 'notifications/newTopic', component: () => import('../views/NewTopic.vue'), meta: { transition: 'expand-card', requireLogin: true } },
    ]
  },

];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to) => {
  if (to.meta.requireLogin && !store.state.firebase.user) {
    return {
      path: '/login',
      // save the location we were at to come back later
      query: {
        redirect: to.fullPath
      }
    }
  }
});

export default router
